<template>
  <div>
    <c-title :text="title" :hide="true"></c-title>
    <p>{{ message }}</p>
  </div>
</template>

<script>
  import {mapState} from 'vuex'
  import cTitle from '@/components/_title.vue'

  export default {
    data () {
      return {
        title: 'ERROR!',
      }
    },
    computed: mapState([
      'message'
    ]),
    mounted: function () {
      this.$store.commit('message', '404 Not Found！');
    },
    components: {cTitle}
  }
</script>


