<template>
  <nav class="navbar navbar-toggleable-md navbar-light bg-faded">
    <button class="navbar-toggler navbar-toggler-right" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>
    <a class="navbar-brand" href="#">Navbar</a>

    <div class="collapse navbar-collapse" id="navbarSupportedContent">
      <ul class="navbar-nav mr-auto">
        <li class="nav-item active">
          <a class="nav-link" href="#">Home <span class="sr-only">(current)</span></a>
        </li>
        <li class="nav-item">
          <a class="nav-link" href="#">Link</a>
        </li>
        <li class="nav-item">
          <a class="nav-link disabled" href="#">Disabled</a>
        </li>
      </ul>
      <form class="form-inline my-2 my-lg-0">
        <button @click="logout" class="btn btn-sm btn-outline-success">Logout</button>
        <button @click="login" class="btn btn-sm btn-outline-success">Login</button>
      </form>
    </div>
  </nav>
</template>

<script>
export default {
  data () {
    return {
      profile: {},
      ready: false,
      authorized: false
    }
  },
  methods:{
    getProfile () {
      let vm = this
      FB.api('/me', function (response) {
        console.log('res in getProfile', response)
        vm.$set(vm, 'profile', response)
      })
    },
    login () {
      let vm = this
      FB.login(function (response) {
        console.log('res in FB.login', response)
        vm.statusChangeCallback(response)
      }, {
        scope: 'email, public_profile',
        return_scopes: true
      })
    },
    logout () {
      let vm = this
      FB.logout(function (response) {
        console.log('res in FB.logout', response)
        vm.statusChangeCallback(response)
      })
    },
    statusChangeCallback (response) {
      let vm = this
      vm.ready = true
      console.log('statusChangeCallback')
      console.log('res in statusChangeCallback', response)
      if (response.status === 'connected') {
        vm.authorized = true
        vm.getProfile()
      } else if (response.status === 'not_authorized') {
        vm.authorized = false
      } else {
        vm.authorized = false
      }
    }
  },
  mounted () {
    let vm = this
    window.fbAsyncInit = () => {
      FB.init({
        appId: '664195880445424',
        cookie: true,
        xfbml: true,
        version: 'v2.8'
      })
      FB.AppEvents.logPageView()
      console.log('getLoginStatus')
      FB.getLoginStatus(function (response) {
        vm.statusChangeCallback(response)
      })
    }
  }
}
</script>

<style lang="scss" scoped>
  button{
    cursor: pointer;
  }
</style>


